import React, { useState, useEffect, useRef } from "react";
import { useSearchParams } from "react-router-dom";
import { useScrollAnimation } from "../hooks/useScrollAnimation";
import HeaderPage from "../components/layout/HeaderPage";
import ProductCard from "../components/ProductCard";
import CategorySidebar from "../components/CategorySidebar";
import { productsData, Product } from "../config/productsData";
import {
  getCategoryDisplayName,
  getFilteredProducts
} from "../utils/productUtils";

const ProductsPage: React.FC = () => {
  const [searchParams] = useSearchParams();
  const [selectedCategory, setSelectedCategory] = useState<string>("all");
  const [filteredProducts, setFilteredProducts] = useState<Product[]>(productsData);
  const [currentPage, setCurrentPage] = useState(1);
  const [searchQuery, setSearchQuery] = useState("");
  const [sortBy, setSortBy] = useState("newest");
  const [isScrolling, setIsScrolling] = useState(false);
  const [scrollProgress, setScrollProgress] = useState(0);
  const productsPerPage = 9;

  // Ref for products section to scroll to
  const productsRef = useRef<HTMLDivElement>(null);

  // Handle URL category parameter
  useEffect(() => {
    const categoryParam = searchParams.get("category");
    if (categoryParam) {
      setSelectedCategory(categoryParam);
    }
  }, [searchParams]);

  // Function to scroll to products section with smooth animation
  const scrollToProducts = () => {
    if (productsRef.current) {
      const headerHeight = 80; // Account for fixed header
      const extraOffset = 120; // Extra space to show search bar
      const targetPosition =
        productsRef.current.offsetTop - headerHeight - extraOffset;

      window.scrollTo({
        top: Math.max(0, targetPosition), // Ensure we don't scroll above page top
        behavior: "smooth",
      });
    }
  };

  // Initialize scroll animations
  useScrollAnimation();

  // Enhanced filter, search and sort logic
  useEffect(() => {
    const filtered = getFilteredProducts(selectedCategory, searchQuery, sortBy);
    setFilteredProducts(filtered);
    setCurrentPage(1); // Reset to first page when filters change
  }, [selectedCategory, searchQuery, sortBy]);

  // Calculate pagination
  const indexOfLastProduct = currentPage * productsPerPage;
  const indexOfFirstProduct = indexOfLastProduct - productsPerPage;
  const currentProducts = filteredProducts.slice(
    indexOfFirstProduct,
    indexOfLastProduct,
  );
  const totalPages = Math.ceil(filteredProducts.length / productsPerPage);

  // Smooth animated scroll to products section
  const scrollToProductsSection = () => {
    if (productsRef.current && !isScrolling) {
      setIsScrolling(true);
      setScrollProgress(0);

      const headerHeight = 80; // Fixed header height
      const extraOffset = 40; // Extra space for better UX
      const targetPosition =
        productsRef.current.offsetTop - headerHeight - extraOffset;
      const finalPosition = Math.max(0, targetPosition);

      // Custom smooth scroll with easing animation
      const startPosition = window.pageYOffset;
      const distance = finalPosition - startPosition;
      const duration = 800; // Animation duration in ms
      let startTime: number | null = null;

      // Easing function for smooth animation (ease-in-out-cubic)
      const easeInOutCubic = (t: number): number => {
        return t < 0.5
          ? 4 * t * t * t
          : (t - 1) * (2 * t - 2) * (2 * t - 2) + 1;
      };

      const animateScroll = (currentTime: number) => {
        if (startTime === null) startTime = currentTime;
        const timeElapsed = currentTime - startTime;
        const progress = Math.min(timeElapsed / duration, 1);

        // Apply easing function
        const easedProgress = easeInOutCubic(progress);
        const currentPosition = startPosition + distance * easedProgress;

        // Update scroll progress for UI feedback
        setScrollProgress(Math.round(progress * 100));

        window.scrollTo(0, currentPosition);

        if (progress < 1) {
          requestAnimationFrame(animateScroll);
        } else {
          // Animation completed - add a subtle bounce effect
          setScrollProgress(100);
          setIsScrolling(false);

          // Add bounce effect to products section
          if (productsRef.current) {
            // Add subtle bounce animation
            productsRef.current.style.transform = "translateY(-8px)";
            productsRef.current.style.transition =
              "all 0.4s cubic-bezier(0.68, -0.55, 0.265, 1.55)";
            productsRef.current.style.boxShadow =
              "0 10px 25px -5px rgba(59, 130, 246, 0.1)";

            setTimeout(() => {
              if (productsRef.current) {
                productsRef.current.style.transform = "translateY(0)";
                productsRef.current.style.boxShadow = "";

                setTimeout(() => {
                  if (productsRef.current) {
                    productsRef.current.style.transition = "";
                    productsRef.current.style.transform = "";
                  }
                }, 400);
              }
            }, 150);
          }
        }
      };

      requestAnimationFrame(animateScroll);
    }
  };

  // Change page with smooth animated scroll
  const paginate = (pageNumber: number) => {
    setCurrentPage(pageNumber);
    // Small delay to ensure state update, then scroll with animation
    setTimeout(() => {
      scrollToProductsSection();
    }, 100); // Increased delay for better state sync
  };

  // Smooth scroll khi i danh mc
  useEffect(() => {
    scrollToProductsSection();
  }, [selectedCategory]);

  // Scroll to top when filters change
  useEffect(() => {
    if (currentPage > 1) {
      scrollToProductsSection();
    }
  }, [filteredProducts.length]);



  return (
    <div className="bg-gray-50 min-h-screen">
      {/* Modern Header */}
      <HeaderPage
        title="Sn phm"
        subtitle="chuyn nghip"
        description="Khm ph b su tp cc sn phm in n, POSM v bao b cht lng cao c thit k v sn xut bi i ng chuyn gia hng u."
        breadcrumbs={[{ label: "Trang ch", href: "/" }, { label: "Sn phm" }]}
        stats={[
          { value: "100+", label: "Sn phm", color: "blue" },
          { value: "10+", label: "Danh mc", color: "purple" },
          { value: "50+", label: "Khch hng", color: "green" },
        ]}
      />

      {/* Main Content with Sidebar Layout */}
      <div className="container mx-auto px-4 py-16">
        <div className="flex flex-col lg:flex-row gap-8">
          {/* Left Sidebar - Category Filter */}
          <div className="lg:w-80 flex-shrink-0">
            <CategorySidebar
              selectedCategory={selectedCategory}
              searchQuery={searchQuery}
              onCategoryChange={(category) => {
                setSelectedCategory(category);
                setTimeout(scrollToProducts, 100);
              }}
              onSearchChange={setSearchQuery}
              totalProducts={productsData.length}
            />
          </div>



          {/* Right Content Area */}
          <div className="flex-1">
            {/* Results Header */}
            <div className="bg-white p-6 rounded-2xl shadow-lg border border-gray-100 mb-8">
              <div className="flex flex-col sm:flex-row sm:items-center sm:justify-between">
                <div>
                  <h3 className="text-lg font-semibold text-gray-900">
                    {selectedCategory === "all"
                      ? "Tt c sn phm"
                      : `Danh mc: ${getCategoryDisplayName(selectedCategory)}`}
                  </h3>
                  <div className="flex items-center gap-4 mt-1">
                    <p className="text-sm text-gray-600">
                      Tm thy {filteredProducts.length} sn phm
                    </p>
                    {searchQuery && (
                      <p className="text-sm text-blue-600">
                        cho "{searchQuery}"
                      </p>
                    )}
                  </div>
                </div>

                {/* Sort Options */}
                <div className="mt-4 sm:mt-0 flex items-center gap-3">
                  <span className="text-sm text-gray-500">Sp xp:</span>
                  <select
                    value={sortBy}
                    onChange={(e) => setSortBy(e.target.value)}
                    className="px-4 py-2 border border-gray-200 rounded-lg text-sm focus:ring-2 focus:ring-blue-500 focus:border-transparent bg-white"
                  >
                    <option value="newest">Mi nht</option>
                    <option value="popular">Ph bin nht</option>
                    <option value="name">Tn A-Z</option>
                  </select>

                  {/* Clear filters button */}
                  {(searchQuery || selectedCategory !== "all") && (
                    <button
                      onClick={() => {
                        setSearchQuery("");
                        setSelectedCategory("all");
                        setSortBy("newest");
                      }}
                      className="px-3 py-2 text-xs text-gray-600 hover:text-gray-800 bg-gray-100 hover:bg-gray-200 rounded-lg transition-all duration-200"
                    >
                      Xa b lc
                    </button>
                  )}
                </div>
              </div>
            </div>

            {/* Products Grid */}
            <div ref={productsRef} id="product-list" className="relative">
              {/* Scroll Animation Overlay */}
              {isScrolling && (
                <div className="absolute inset-0 bg-white/80 backdrop-blur-sm z-10 flex items-center justify-center rounded-2xl">
                  <div className="flex flex-col items-center gap-3 bg-white px-8 py-4 rounded-2xl shadow-lg border border-gray-200">
                    <div className="flex items-center gap-3">
                      <div className="w-5 h-5 border-2 border-blue-600 border-t-transparent rounded-full animate-spin"></div>
                      <span className="text-sm font-medium text-gray-700">
                        ang chuyn trang...
                      </span>
                    </div>

                    {/* Progress Bar */}
                    <div className="w-32 h-1 bg-gray-200 rounded-full overflow-hidden">
                      <div
                        className="h-full bg-gradient-to-r from-blue-500 to-blue-600 rounded-full transition-all duration-100 ease-out"
                        style={{ width: `${scrollProgress}%` }}
                      ></div>
                    </div>

                    <span className="text-xs text-gray-500">
                      {scrollProgress}%
                    </span>
                  </div>
                </div>
              )}

              {filteredProducts.length === 0 ? (
                <div className="text-center py-20 bg-white rounded-2xl shadow-lg border border-gray-100">
                  <svg
                    className="w-20 h-20 text-blue-300 mx-auto mb-6"
                    fill="none"
                    stroke="currentColor"
                    viewBox="0 0 24 24"
                  >
                    <path
                      strokeLinecap="round"
                      strokeLinejoin="round"
                      strokeWidth="2"
                      d="M9.172 16.172a4 4 0 015.656 0M9 10h.01M15 10h.01M12 16a4 4 0 100-8 4 4 0 000 8z"
                    />
                  </svg>
                  <h3 className="text-2xl font-bold text-gray-800 mb-3">
                    Khng tm thy sn phm no
                  </h3>
                  <p className="text-gray-600 mb-8 text-lg">
                    Khng c sn phm no trong danh mc ny
                  </p>
                  <button
                    className="px-8 py-3 bg-blue-600 text-white rounded-lg font-semibold hover:bg-blue-700 transition-all duration-300 shadow-lg hover:shadow-xl transform hover:-translate-y-1"
                    onClick={() => {
                      setSelectedCategory("all");
                      setTimeout(scrollToProducts, 100);
                    }}
                  >
                    Xem tt c sn phm
                  </button>
                </div>
              ) : (
                <div
                  className={`grid grid-cols-1 sm:grid-cols-2 xl:grid-cols-3 gap-6 transition-opacity duration-300 ${
                    isScrolling ? "opacity-50" : "opacity-100"
                  }`}
                >
                  {currentProducts.map((product, idx) => (
                    <ProductCard
                      key={product.slug}
                      product={product}
                      index={idx}
                    />
                  ))}
                </div>
              )}

              {/* Pagination */}
              {totalPages > 1 && (
                <div className="mt-12 flex justify-center">
                  <nav
                    className="flex items-center gap-2 bg-white p-4 rounded-xl shadow-lg border border-gray-100"
                    aria-label="Pagination"
                  >
                    <button
                      onClick={() =>
                        currentPage > 1 && paginate(currentPage - 1)
                      }
                      disabled={currentPage === 1}
                      className={`px-4 py-2 rounded-lg text-sm font-semibold transition-all duration-300 ${
                        currentPage === 1
                          ? "text-gray-300 cursor-not-allowed"
                          : "text-gray-700 hover:bg-blue-50 hover:text-blue-700"
                      }`}
                    >
                      Trc
                    </button>
                    {Array.from({ length: totalPages }, (_, i) => i + 1)
                      .filter(
                        (num) =>
                          num === 1 ||
                          num === totalPages ||
                          Math.abs(num - currentPage) <= 1,
                      )
                      .map((number, index, array) => {
                        if (index > 0 && array[index - 1] !== number - 1) {
                          return (
                            <React.Fragment key={`ellipsis-${number}`}>
                              <span className="px-2 py-1 text-gray-400">
                                ...
                              </span>
                              <button
                                onClick={() => paginate(number)}
                                className={`px-4 py-2 rounded-lg text-sm font-semibold transition-all duration-300 ${
                                  currentPage === number
                                    ? "bg-blue-600 text-white shadow-lg"
                                    : "text-gray-700 hover:bg-blue-50 hover:text-blue-700"
                                }`}
                              >
                                {number}
                              </button>
                            </React.Fragment>
                          );
                        }
                        return (
                          <button
                            key={number}
                            onClick={() => paginate(number)}
                            className={`px-4 py-2 rounded-lg text-sm font-semibold transition-all duration-300 ${
                              currentPage === number
                                ? "bg-blue-600 text-white shadow-lg"
                                : "text-gray-700 hover:bg-blue-50 hover:text-blue-700"
                            }`}
                          >
                            {number}
                          </button>
                        );
                      })}
                    <button
                      onClick={() =>
                        currentPage < totalPages && paginate(currentPage + 1)
                      }
                      disabled={currentPage === totalPages}
                      className={`px-4 py-2 rounded-lg text-sm font-semibold transition-all duration-300 ${
                        currentPage === totalPages
                          ? "text-gray-300 cursor-not-allowed"
                          : "text-gray-700 hover:bg-blue-50 hover:text-blue-700"
                      }`}
                    >
                      Tip
                    </button>
                  </nav>
                </div>
              )}

              {/* Results count */}
              {filteredProducts.length > 0 && (
                <div className="mt-6 text-center text-sm text-gray-500">
                  Hin th {indexOfFirstProduct + 1}-
                  {Math.min(indexOfLastProduct, filteredProducts.length)} trn
                  tng s {filteredProducts.length} sn phm
                  {selectedCategory !== "all" && (
                    <span>
                      {' trong danh mc "'}
                      <span className="font-medium text-gray-700">
                        {getCategoryDisplayName(selectedCategory)}
                      </span>
                      {'"'}
                    </span>
                  )}
                </div>
              )}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default ProductsPage;

// CSS Animations for loading
const styles = `
  .loading-dots::after {
    content: '';
    display: inline-block;
    width: 1em;
    text-align: left;
    animation: loading-dots 1.5s steps(3, end) infinite;
  }

  @keyframes loading-dots {
    0%, 20% { content: ''; }
    40% { content: '.'; }
    60% { content: '..'; }
    80%, 100% { content: '...'; }
  }

  .loading-progress {
    animation: loading-progress 2s ease-in-out infinite;
  }

  @keyframes loading-progress {
    0% { width: 0%; }
    50% { width: 70%; }
    100% { width: 100%; }
  }

  /* Enhanced spin animation */
  @keyframes spin {
    from { transform: rotate(0deg); }
    to { transform: rotate(360deg); }
  }

  .animate-spin {
    animation: spin 1s linear infinite;
  }

  /* Pulse animation for center dot */
  @keyframes pulse {
    0%, 100% { opacity: 1; transform: scale(1); }
    50% { opacity: 0.7; transform: scale(0.8); }
  }

  .animate-pulse {
    animation: pulse 2s cubic-bezier(0.4, 0, 0.6, 1) infinite;
  }
`;

// Inject styles
if (typeof document !== "undefined") {
  const styleSheet = document.createElement("style");
  styleSheet.textContent = styles;
  document.head.appendChild(styleSheet);
}
