import { useEffect } from "react";
import { useLocation } from "react-router-dom";

/**
 * Component t ng cun ln u trang khi chuyn route
 * t component ny trong App.tsx, bn trong Router v trc Routes
 */
const ScrollToTop: React.FC = () => {
  const { pathname } = useLocation();

  useEffect(() => {
    // Cun ln u trang vi hnh vi mt m
    window.scrollTo({
      top: 0,
      behavior: "smooth",
    });
  }, [pathname]);

  return null; // Component ny khng render bt k UI no
};

export default ScrollToTop;
