import { useEffect } from 'react';

export interface SEOData {
  title: string;
  description: string;
  keywords?: string;
  ogTitle?: string;
  ogDescription?: string;
  ogImage?: string;
  ogUrl?: string;
  canonicalUrl?: string;
  structuredData?: object;
}

export const useSEO = (seoData: SEOData) => {
  useEffect(() => {
    // Set document title
    document.title = seoData.title;

    // Helper function to set or update meta tag
    const setMetaTag = (name: string, content: string, property?: boolean) => {
      const attribute = property ? 'property' : 'name';
      let meta = document.querySelector(`meta[${attribute}="${name}"]`) as HTMLMetaElement;
      
      if (!meta) {
        meta = document.createElement('meta');
        meta.setAttribute(attribute, name);
        document.head.appendChild(meta);
      }
      
      meta.setAttribute('content', content);
    };

    // Set basic meta tags
    setMetaTag('description', seoData.description);
    if (seoData.keywords) {
      setMetaTag('keywords', seoData.keywords);
    }

    // Set Open Graph tags
    setMetaTag('og:title', seoData.ogTitle || seoData.title, true);
    setMetaTag('og:description', seoData.ogDescription || seoData.description, true);
    setMetaTag('og:type', 'website', true);
    
    if (seoData.ogImage) {
      setMetaTag('og:image', seoData.ogImage, true);
    }
    
    if (seoData.ogUrl) {
      setMetaTag('og:url', seoData.ogUrl, true);
    }

    // Set Twitter Card tags
    setMetaTag('twitter:card', 'summary_large_image');
    setMetaTag('twitter:title', seoData.ogTitle || seoData.title);
    setMetaTag('twitter:description', seoData.ogDescription || seoData.description);
    
    if (seoData.ogImage) {
      setMetaTag('twitter:image', seoData.ogImage);
    }

    // Set canonical URL
    if (seoData.canonicalUrl) {
      let canonical = document.querySelector('link[rel="canonical"]') as HTMLLinkElement;
      if (!canonical) {
        canonical = document.createElement('link');
        canonical.setAttribute('rel', 'canonical');
        document.head.appendChild(canonical);
      }
      canonical.setAttribute('href', seoData.canonicalUrl);
    }

    // Set structured data
    if (seoData.structuredData) {
      let script = document.querySelector('script[type="application/ld+json"]');
      if (!script) {
        script = document.createElement('script');
        script.setAttribute('type', 'application/ld+json');
        document.head.appendChild(script);
      }
      script.textContent = JSON.stringify(seoData.structuredData);
    }

    // Cleanup function
    return () => {
      // Remove structured data script when component unmounts
      const structuredDataScript = document.querySelector('script[type="application/ld+json"]');
      if (structuredDataScript) {
        structuredDataScript.remove();
      }
    };
  }, [seoData]);
};

